<style scoped>
.back-edit-wrap {
  height: 100%;
  padding: 15px;
  overflow: auto;
}

.breadcrumb {
  margin-bottom: 60px;
}

.back-edit-form {
  width: 600px;
  margin: 0 auto;
}
</style>


<template>
  <section class="back-edit-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{'path': '/backcharacterlist'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="back-edit-form">
      <el-form label-position="top" label-width="80px" :model="adminUser" :rules="rules" ref="adminUser">      
        <el-form-item label="" prop="username">
          <el-input v-model="adminUser.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="adminUser.password" placeholder="6"></el-input>
        </el-form-item>        
        <el-form-item label="" prop="phone">
          <el-input v-model.number="adminUser.phone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="role">
            <el-select v-model="adminUser.role" placeholder="">
              <el-option v-for="role in roles" :key="role.id" :label="role.display_name" :value="role.id"></el-option>
            </el-select>
          <el-button type="text" @click="moreRole" style="margin-top: 10px;">{{roleFlag?'':''}}</el-button>
          </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('adminUser')" style="width: 100%;margin-top: 10px;"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      adminUser: {
        username: "",
        phone: null,
        password: "",
        role: ""
      },

      rules: {
        username: [{ required: true, message: "" }],
        phone: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ],
        password: [
          { required: true, message: "" },
          { min: 6, message: " 6 " }
        ]
      },

      roles: [],
      page: 1,
      roleFlag: false
    };
  },

  created() {
    // const user = this.$route.params.user;
    // if (user) {
    //   this.adminUser.name = user.name;
    //   this.adminUser.password = user.password;
    //   this.adminUser.authorities = user.authority;
    // }

    // let arr = [];
    // for (let i = 0; i < this.$common.authorities.length; i++) {
    //   arr.push(i);
    // }
    // this.authoritiesArr = arr;
    this.$api.getRoleList("", res => {
      this.roles = res.data.data;
    });
  },

  methods: {
    moreRole() {
      if (this.roleFlag) {
        return false;
      }
      this.$api.getRoleList({ page: ++this.page }, res => {
        const data = res.data.data;
        if (data.length) {
          this.roles = [...this.roles, ...data];
        } else {
          this.roleFlag = true;
        }
      });
    },

    /*
    * 
    */
    submitForm(formName) {
      if (!this.adminUser.role) {
        this.$message({
          type: "warning",
          message: ""
        });
        return false;
      }
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$api.postNewAdmin(this.adminUser, res => {
            this.$message({
              type: "success",
              message: "",
              showClose: true
            });
            this.$router.push("/backcharacterlist");
          });
        } else {
          this.$message({
            type: "warning",
            message: "",
            showClose: true
          });
          return false;
        }
      });
    }
  }
};
</script>



