<style>
.user-wrap {
  height: 100%;
  padding: 15px;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.breadcrumb {
  margin-bottom: 20px;
}

.user-wrap .el-input-group__prepend {
  width: 200px;
}

.user-operation {
  margin-bottom: 20px;
  text-align: right;
}

.user-picker > div {
  margin-right: 20px;
}

.user-wrap .table-list {
  flex: 1;
  overflow: auto;
}

.pages {
  text-align: right;
}
</style>

<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="user-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <div class="user-operation">
      <div class="text-align: right">
        <el-input placeholder="" v-model="selectInput" style="margin-bottom: 20px;width: 900px;" @keyup.enter.native="selectSearch">
          <el-select v-model="select" slot="prepend" placeholder="">
            <el-option label="" value="username"></el-option>
            <el-option label="" value="user_id"></el-option>
          </el-select>
          <el-button slot="append" icon="search" @click="selectSearch"></el-button>
        </el-input>
      </div>
      <div class="user-picker">
        <el-select v-model="searchForm.level" filterable placeholder="" style="text-align: left;">
          <el-option v-for="item in levels" :key="item.id" :label="item.name" :value="item.id">
          </el-option>
        </el-select>
        <!-- <el-select v-model="searchForm.published" filterable placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select> -->
        <el-date-picker v-model="dateRange" type="daterange" align="right" placeholder="" :picker-options="dateOptions">
        </el-date-picker>
        <el-button type="primary" icon="search" @click="pickerSearch"></el-button>
      </div>
    </div>

    <div class="table-list">
      <el-table ref="multipleTable" :data="userList" border stripe tooltip-effect="dark" style="width: 100%">
        <el-table-column label="ID" prop="id" sortable>
        </el-table-column>
        <el-table-column prop="username" label="">
          <template slot-scope="scope">
            <el-button type="text" @click="goToUser(scope.row.id)">{{scope.row.username}}</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="">
        </el-table-column>
        <el-table-column label="" prop="member.level" sortable>
          <template slot-scope="scope">
            <span>{{scope.row.member?levels[scope.row.member.level].name: ''}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="score" label="">
          <template slot-scope="scope">{{scope.row.score + ' '}}</template>
        </el-table-column>
        <el-table-column prop="commodity_count" label="">
          <template slot-scope="scope">{{scope.row.commodity_count + ' '}}</template>
        </el-table-column>
        <el-table-column prop="enable_count" label="">
          <template slot-scope="scope">{{scope.row.enable_count + ' '}}</template>
        </el-table-column>
        <el-table-column prop="disable_count" label="">
          <template slot-scope="scope">{{scope.row.disable_count + ' '}}</template>
        </el-table-column>
        <!-- <el-table-column prop="memberLevel" label="">
          <template slot-scope="scope">{{levels[scope.row.memberLevel].name}}</template>
        </el-table-column> -->
        <el-table-column prop="phone" label="">
        </el-table-column>
        <el-table-column prop="created_at" label="">
        </el-table-column>
        <!-- <el-table-column prop="publish_times" label="">
          <template slot-scope="scope">{{scope.row.publish_times + ' '}}</template>
        </el-table-column> -->
        <el-table-column prop="state" label="">
          <template slot-scope="scope">
            <span class="success" v-if="scope.row.state == 1"></span>
            <span class="warning" v-else></span>
          </template>
        </el-table-column>
        <el-table-column label="" width="200">
          <template slot-scope="scope">
            <el-button v-if="scope.row.state == 1" size="small" type="danger" @click="handleStop(scope.$index, scope.row)"></el-button>
            <el-button v-else size="small" type="info" @click="handleNormal(scope.$index, scope.row)"></el-button>
            <el-button size="small" type="danger" @click="handleChange(scope.$index, scope.row)"></el-button>
            <!-- <el-button size="small" type="warning" @click="handleAddRole(scope.$index, scope.row)"></el-button> -->
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="pages">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="page" :page-size="eachPage" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div>

    <el-dialog title="" center :visible.sync="levelUpDialog">
      <el-select v-model="levelUp.level" filterable placeholder="" style="text-align: left;">
        <el-option v-if="item.id != 0" v-for="item in levels" :key="item.id" :label="item.name" :value="item.id">
        </el-option>
      </el-select>

      <el-button type="primary" @click="changeLevel"></el-button>
    </el-dialog>
    
    <el-dialog title="" center :visible.sync="roleDialog">
      <el-select v-model="roleId" filterable placeholder="" style="text-align: left;">
        <el-option v-for="item in roles" :key="item.id" :label="item.name" :value="item.id">
        </el-option>
      </el-select>
      <el-button type="text" @click="getMoreRole">{{roleFlag?'':''}}</el-button>
      <div></div>
      <el-button type="primary" @click="changeRole" style="width: 300px;margin-top: 30px;"></el-button>
    </el-dialog>
  </section>
</template>

<script>
export default {
  data() {
    return {
      loading: true,

      levels: this.$common.memberLevels,

      page: 1,
      eachPage: 10,
      count: 0,

      select: "username",
      selectInput: "",

      searchForm: {
        level: ""
      },
      levelUpDialog: false,

      levelUp: {
        level: 1,
        user_id: ""
      },
      currentUserIndex: 0,
      currentUser: {},

      dateRange: "",

      dateOptions: this.$common.dateOptions,

      //
      userList: [],

      roles: [],
      rolePage: 1,
      roleId: "",
      roleDialog: false
    };
  },

  created() {
    this.$api.getUserList("", res => {
      this.userList = res.data.data;
      this.count = res.data.count;
      this.loading = false;
    });
  },

  methods: {

    //
    goToUser(id){
      this.$router.push({name: 'usersingle', params: {id: id}})
    },
    //
    handleAddRole(index, row) {
      this.roleDialog = true;
      this.currentUser = row;
      this.currentUserIndex = index;
    },

    changeRole() {
      if (this.roleId) {
        const postData = {
          user_id: this.currentUser.id,
          role_id: this.roleId
        };
        this.$api.postNewRole(postData, res => {
          this.roleDialog = false;
          // this.userList[this.currentUserIndex]
          this.$message({
            type: "success",
            message: ""
          });
        });
      }
    },
    //
    getMoreRole() {
      this.$api.getRoleList({ page: ++this.rolePage }, res => {
        if (this.roleFlag) {
          return false;
        }
        if (res.data.data.length) {
          this.roles = [...this.roles, ...res.data.data];
        } else {
          this.roleFlag = true;
        }
      });
    },
    /*
    * 
    */
    selectSearch() {
      const getData = {
        [this.select]: this.selectInput
      };
      this.$api.getUserList(getData, res => {
        this.userList = res.data.data;
        this.count = res.data.count;
      });
    },

    /*
    * 
    */
    pickerSearch() {
      if (this.dateRange) {
        this.searchForm.start = new Date(this.dateRange[0]);
        this.searchForm.end = new Date(this.dateRange[1]);
      }
      this.$api.getUserList(this.searchForm, res => {
        this.userList = res.data.data;
        this.count = res.data.count;
      });
    },

    /*
    * 
    */
    handleStop(index, row) {
      this.$operation.tableMessageBox("", () => {
        this.$api.changeUser(row.id, { state: 0 }, res => {
          this.userList[index].state = 0;
          this.$message({
            type: "success",
            message: "!"
          });
        });
      });
    },

    //
    handleNormal(index, row) {
      this.$operation.tableMessageBox("", () => {
        this.$api.changeUser(row.id, { state: 1 }, res => {
          this.userList[index].state = 1;
          this.$message({
            type: "success",
            message: "!"
          });
        });
      });
    },

    /*
    * 
    */
    handleChange(index, row) {
      this.levelUpDialog = true;
      this.currentUser = row;
      this.currentUserIndex = index;
    },

    changeLevel() {
      this.levelUp.user_id = this.currentUser.id;
      if (this.levelUp) {
        this.$api.postLevelUp(this.levelUp, res => {
          this.levelUpDialog = false;
          this.userList[
            this.currentUserIndex
          ].member.level = this.levelUp.level;
          this.$message({
            type: "success",
            message: ""
          });
        });
      } else {
        this.$message({
          type: "warning",
          message: ""
        });
      }
    },

    /*
    * 
    */
    handleCurrentChange(page) {
      let getData = {
        page: page
      };
      if (this.selectInput) {
        getData[this.select] = this.selectInput;
      } else {
        getData = Object.assign({ page }, this.searchForm);
      }
      this.$api.getUserList(getData, res => {
        this.userList = res.data.data;
      });
    }
  }
};
</script>



