<style>
.check-in-wrap {
  height: 100%;
  padding: 15px;
  overflow: auto;
}

.breadcrumb {
  margin-bottom: 20px;
}
.start-img-uploader {
  text-align: center;
}

.start-img-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  margin-bottom: 20px;
}

.start-img-uploader .el-upload:hover {
  border-color: #20a0ff;
}

.start-img-uploader .start-img-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

.start-img {
  max-width: 100%;
  display: block;
}

.share-form {
  max-width: 900px;
  margin: 0 auto;
}
</style>

<template>
  <section v-if="loading" class="loading">
    <i class="el-icon-loading"></i>
  </section>
  <section v-else class="check-in-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="share-form">
      <el-form label-position="top" label-width="80px" :model="shareForm" :rules="rules" ref="shareForm">
        <el-form-item label="" prop="time">
          <el-date-picker v-model="shareForm.time" type="datetimerange" :picker-options="dateRangeOption" placeholder="" align="left">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="score">
          <el-input v-model.number="shareForm.score" placeholder="">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="daily_max">
          <el-input v-model.number="shareForm.daily_max" placeholder="">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="H5" prop="content">
          <el-input type="textarea" v-model="shareForm.content"></el-input>
        </el-form-item>
        <el-form-item label="APP" prop="rule">
          <el-input type="textarea" v-model="shareForm.rule"></el-input>
        </el-form-item>
        <el-form-item label="H5" prop="image">
          <el-upload class="start-img-uploader" :action="host" with-credentials name="image" :multiple="false" accept="image/jpg,image/png,image/jpeg" :show-file-list="false" :on-success="handleSuccess" :before-upload="beforeUpload">
            <img v-if="shareForm.image" :src="shareForm.image" class="start-img">
            <i v-else class="el-icon-plus start-img-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit('shareForm')" style="width: 100%;margin-top: 20px;"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      host: this.$common.host + "upload",
      shareForm: {
        score: "",
        daily_max: "",
        content: "",
        rule: "",
        time: "",
        image: ""
      },

      rules: {
        score: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ],
        daily_max: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ],
        time: [{ required: true, message: "" }]
      },

      dateRangeOption: this.$common.dateOptions
      // dateRange: ""
    };
  },

  methods: {
    /*
      
    */
    beforeUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isLt2M;
    },

    /*
      
    */
    handleSuccess(res, file) {
      this.shareForm.image = res.data.base_url;
    },
    /*
    * 
    */
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.shareForm.start = this.shareForm.time[0];
          this.shareForm.end = this.shareForm.time[1];
          this.$api.postShare(this.shareForm, res => {
            this.$message({
              type: "success",
              message: "",
              showClose: true
            });
            this.$router.push("/membershare");
          });
        } else {
          this.$message({
            type: "warning",
            message: "",
            showClose: true
          });
          return false;
        }
      });
    }
  }
};
</script>



