<style scoped>
.back-character-edit-wrap {
  height: 100%;
  padding: 15px;
  overflow: auto;
}
.breadcrumb {
  margin-bottom: 60px;
}

.back-character-form {
  width: 600px;
  margin: 0 auto;
}
</style>

<template>
  <section class="back-character-edit-wrap">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{path: 'backcharacterlist'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <div class="back-character-form">
      <el-form label-position="top" label-width="80px" :model="formCharacter" :rules="rules" ref="formCharacter">
        <el-form-item label="" prop="name">
          <el-input v-model="formCharacter.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="display_name">
          <el-input v-model="formCharacter.display_name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="formCharacter.description" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('formCharacter')" style="width: 100%;margin-top: 10px;"></el-button>
        </el-form-item>
      </el-form>
    </div>
        
  </section>
</template>

<script>
export default {
  data() {
    return {
      authorities: [],
      isIndeterminate: true,
      checkAll: true,
      authoritiesArr: [],

      rules: {
        name: [{ required: true, message: "" }],
        display_name: [{ required: true, message: "" }]
      },

      formCharacter: {
        name: "",
        display_name: "",
        description: "",
      }
    };
  },
  created() {
  },
  methods: {
    /*
    * 
    */
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$api.temp(this.formCharacter, res => {
            this.$message({
              type: "success",
              message: "",
              showClose: true
            });
          });
        } else {
          this.$message({
            type: "warning",
            message: "",
            showClose: true
          });
          return false;
        }
      });
    }
  }
};
</script>



